import { Articolo } from './../articolo';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class ArticoliService {

  private articoliUrl = 'http://localhost:8080/ArticoliWeb/rest/articolos';
 
  constructor(
    private http: HttpClient) { }
 
  /** GET Articoli dal server */
  getArticoli (): Observable<Articolo[]> {
    return this.http.get<Articolo[]>(this.articoliUrl)
      .pipe(
        tap(articoli => this.log(`recuperati articoli`)),
        catchError(this.handleError('getArticoli', []))
      );
  }
 
  /** GET articolo by id. Return `undefined` when id not found */
  getArticolooNo404<Data>(id: number): Observable<Articolo> {
    const url = `${this.articoliUrl}/?id=${id}`;
    return this.http.get<Articolo[]>(url)
      .pipe(
        map(heroes => heroes[0]), // returns a {0|1} element array
        tap(h => {
          const outcome = h ? `fetched` : `did not find`;
          this.log(`${outcome} articolo id=${id}`);
        }),
        catchError(this.handleError<Articolo>(`getArticolo id=${id}`))
      );
  }
 
  /** GET articolo by id. Will 404 if id not found */
  getArticolo(id: number): Observable<Articolo> {
    const url = `${this.articoliUrl}/${id}`;
    return this.http.get<Articolo>(url).pipe(
      tap(_ => this.log(`fetched articolo id=${id}`)),
      catchError(this.handleError<Articolo>(`getHero id=${id}`))
    );
  }
 
 
  //////// Save methods //////////
 
  /** POST: add a new articolo to the server */
  addArticolo (articolo: Articolo): Observable<Articolo> {
    return this.http.post<Articolo>(this.articoliUrl, articolo, httpOptions).pipe(
      tap((articolo: Articolo) => this.log(`added articolo w/ id=${articolo.id}`)),
      catchError(this.handleError<Articolo>('addArticolo'))
    );
  }
 
  /** DELETE: delete the articolo from the server */
  deleteArticolo (articolo: Articolo | number): Observable<Articolo> {
    const id = typeof articolo === 'number' ? articolo : articolo.id;
    const url = `${this.articoliUrl}/${id}`;
 
    return this.http.delete<Articolo>(url, httpOptions).pipe(
      tap(_ => this.log(`deleted articolo id=${id}`)),
      catchError(this.handleError<Articolo>('deleteArticolo'))
    );
  }
 
  /** PUT: update the articolo on the server */
  updateArticolo (articolo: Articolo): Observable<any> {
    return this.http.put(this.articoliUrl, articolo, httpOptions).pipe(
      tap(_ => this.log(`updated articolo id=${articolo.id}`)),
      catchError(this.handleError<any>('updateArticolo'))
    );
  }
 
  /**
   * Handle Http operation that failed.
   * Let the app continue.
   * @param operation - name of the operation that failed
   * @param result - optional value to return as the observable result
   */
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
 
      // TODO: send the error to remote logging infrastructure
      console.log(error); // log to console instead
 
      // TODO: better job of transforming error for user consumption
      this.log(`${operation} failed: ${error.message}`);
 
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
 
  /** Log a HeroService message with the MessageService */
  private log(message: string) {
    console.log('HeroService: ' + message);
  }
}

